.DesktopEmail {
	z-index: 1;
	position: absolute;
	width: 100%;
	pointer-events: none;
	.desktopEmailWrapper {
		position: relative;
		grid-column: 7 / -1;
		margin-left: 20px;
		visibility: hidden;
		span {
			overflow: hidden;
			display: block;
			span {
				display: block;
				transform: translateY(100%);
				transition: transform 0.6s cubic-bezier(.5,0,0,1),-webkit-transform 1s cubic-bezier(.5,0,0,1);
				h3 {
					color: white;
					font-family: Overpass Extra Bold;
					width: 337px; } } }
		label {
			color: white;
			font-family: Overpass;
			position: absolute;
			bottom: 0;
			pointer-events: none;
			opacity: 0;
			transition: opacity 0.4s 0.3s, transform 0.15s, color 0.15s;
			// transition-delay: opacity 0.5s
			h4 {
				transition: font-size 0.15s; } }
		.submitIcon {
			position: absolute;
			left: 315px;
			bottom: 10px;
			opacity: 0;
			transition: opacity 0.4s 0.3s;
 }			// transition-delay: opacity 0.5s
		input[type="email"] {
			margin: 3.5em 0em 0em 0em;
			border-color: white;
			width: 337px;
			color: white;
			opacity: 0;
			transition: opacity 0.4s 0.3s, border-color 0.15s;
 } }			// transition-delay: opacity 0.5s
	// .hold-icon
	// 	opacity: 0
	// 	visibility: hidden
	// 	transition: opacity 0.3s 0.4s
	// 	p
	// 		color: white
	&.active {
		pointer-events: none;
		.desktopEmailWrapper {
			visibility: visible;
			span {
				span {
					transform: translateY(0%); } }
			label {
				opacity: 1; }
			.submitIcon {
				opacity: 1; }
			input[type="email"] {
				pointer-events: all;
				opacity: 1; } }
		// .hold-icon
		// 	position: relative
		// 	width: 120px
		// 	visibility: visible
 }		// 	opacity: 1
	&.focused {
		.desktopEmailWrapper {
			label {
				transform: translateY(-100%);
				color: #717171;
				h4 {
					font-size: 1em; } }
			input[type="email"] {
				border-color: #C247C3; } } } }
@media (min-width: 1280px) {
	.DesktopEmail {
		.desktopEmailWrapper {
			grid-column: 8 / -1;
			margin-left: 20px;
			span {
				span {
					h3 {} } }
			input[type="email"] {
 } } } }				// margin: 0
